.Catalog{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 5rem;
}

.Catalog_cards{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    padding: 0rem 2rem;
    width: calc(100% - 4rem);
    max-width: 1440px;
}

.Catalog_cards .Card:hover{
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.40);
}